.agitation {
  padding-top: 54px;
  padding-bottom: 58px;
}
.agitation__text {
  color: #283645;
  text-align: center;
  line-height: 30px;
  margin: 0;
}
.comment {
  color: #283645;
  line-height: 24px;
  padding-top: 25px;
}
.comment__user-name {
  font-weight: bold;
  margin: 0;
}
.comment__time {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 300;
}
.download {
  color: #ffffff;
  background-color: #1d2631;
}
.download__button {
  font-size: 18px;
  line-height: 18px;
}
.download__availability {
  line-height: 24px;
  padding-bottom: 34px;
  margin: 0;
}
.download__image {
  padding-bottom: 28px;
}
.download__link {
  padding-bottom: 32px;
}
.download__logo {
  padding-bottom: 28px;
}
.logo-wrapper {
  background-color: #1d2631;
}
.main-nav {
  background-color: #283645;
}
.offer__opportunity {
  background-color: #f2f2f2;
  padding-top: 50px;
}
.offer__question {
  padding-top: 46px;
  padding-bottom: 37px;
  margin: 0;
  font-size: 24px;
  color: #283645;
}
.offer__slogan {
  margin: 0;
  padding-bottom: 50px;
  font-size: 24px;
  color: #283645;
}
.offer__image {
  text-align: center;
}
.offer__header {
  text-align: center;
}
.opportunity__item-head {
  margin: 0;
  padding-bottom: 22px;
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
  text-align: center;
  color: #283645;
}
.opportunity__item-body {
  margin: 0;
  padding-bottom: 40px;
  text-align: center;
  color: #283645;
}
@font-face {
  font-family: "Open Sans";
  src: url("../../fonts/opensans.woff") format("woff"), url("../../fonts/opensansbold.woff") format("woff"), url("../../fonts/opensanslight.woff") format("woff"), url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensanslight.woff2") format("woff2");
}
body {
  font-size: 14px;
  font-family: "Open Sans", "Arial", sans-serif;
  line-height: 24px;
}
a {
  color: #ffffff;
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-footer {
  padding-top: 40px;
  width: 500px;
  height: 280px;
}
.page-footer__logo {
  background: green;
  width: 150px;
  height: 40px;
  margin-bottom: 40px;
}
.page-footer__social-links {
  width: 150px;
  height: 40px;
  background: pink;
  margin-bottom: 43px;
}
.page-footer__develop {
  width: 150px;
  height: 40px;
  background-color: blue;
}
.page-footer__develop-link {
  color: #283645;
  line-height: 18px;
}
.page-header__title {
  color: #ffffff;
  font-size: 24px;
  font-weight: 300;
  margin-top: 0;
  padding-top: 35px;
  text-align: center;
}
.page-header__photo {
  margin: 0;
}
.photo__wrapper-image {
  background-image: url("/source/img/bg-back-mobile@1x.jpg");
  background-color: #2b3b4b;
  background-repeat: no-repeat;
  width: 320px;
  height: 240px;
}
.site-list {
  padding: 0;
  margin: 0;
}
.site-list__item {
  text-transform: uppercase;
  font-size: 18px;
  list-style: none;
  text-align: center;
  padding: 25px;
  border-bottom: 1px solid #000001;
  border-top: 1px solid #485461;
}
.user-photo__comment {
  background-color: #f2f2f2;
}
.user-photo__name {
  text-transform: uppercase;
  background-color: #d22856;
  color: #ffffff;
}
